.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  img {
    height: 1.5em;
    vertical-align: sub;
  }
}

img.legend {
  height: 5em;
}

.people {
  display: flex;
  flex-direction: row;
  align-items: stretch;
}

.person {
  padding-right: 1.5em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  img.adult {
    height: 4em;
  }
  img.child {
    height: 3em;
  }
}

.status-icons {
  img {
    height: 1.5em;
  }
}
